article{
    margin-top: 20px;
}
.error{
    width: 300px;
    height: 80px;
    background: #eee;
     border-radius: 10px;
    font-size: 1.2em;
    text-transform: capitalize;
    display: flex;
    justify-content: center;
    text-align: center;
    transition: 1s;
    align-items: center;
     position: absolute;
    left: calc(50% - 150px);
    top:10px;
}
.ani{
    transition: 1s;
}
#errorbox{
    display: none;
}
#errorbox2{
    display: none;
}
#spinbox{
    background: #00000080;
    position: absolute;;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    display: none;
 }

#loader{

    width: 80px;
    height: 80px;
    border-radius: 100%;
    border: 5px solid ;
    border-bottom-color: rgb(229, 255, 0);
    border-top-color: rgb(0, 255, 157);
    border-right-color: rgb(0, 204, 255);
    border-left-color: yellow;
     position: absolute;
     top: 50%;
     left: calc(50% - 40px);
animation: spin 1s infinite linear;
 


}

@keyframes spin{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}